
'use client';

import React, { useState, useEffect } from 'react';
import Link from 'next/link';
import { useAuth } from '@/hooks/use-auth';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Progress } from '@/components/ui/progress';
import { Input } from '@/components/ui/input';
import { LogOut, Send, History, Shield, MoreHorizontal, CheckCircle, XCircle } from 'lucide-react';
import { RobuxIcon } from '@/components/robux-icon';
import { useForm } from 'react-hook-form';
import { zodResolver } from '@hookform/resolvers/zod';
import * as z from 'zod';
import { Form, FormControl, FormField, FormItem, FormLabel, FormMessage } from '@/components/ui/form';
import { Table, TableBody, TableCell, TableHead, TableHeader, TableRow } from "@/components/ui/table";
import { Badge } from "@/components/ui/badge";
import { format } from "date-fns";
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';

// --- Helper Components from original files ---

function Header() {
  return (
    <div className="flex items-center justify-center gap-3 py-8">
      <RobuxIcon className="h-10 w-10 text-primary" />
      <h1 className="text-4xl font-bold tracking-tighter text-gray-800 dark:text-white">
        Robux <span className="text-primary">Flow</span>
      </h1>
    </div>
  );
}

// --- LoginView Component (merged) ---

const loginFormSchema = z.object({
  email: z.string().email({ message: 'Please enter a valid email address.' }),
  password: z.string().min(6, { message: 'Password must be at least 6 characters.' }),
});

type LoginFormValues = z.infer<typeof loginFormSchema>;

function AuthForm({ isSignUp, onSubmit, loading }: { isSignUp: boolean; onSubmit: (values: LoginFormValues) => void; loading: boolean; }) {
  const form = useForm<LoginFormValues>({
    resolver: zodResolver(loginFormSchema),
    defaultValues: { email: '', password: '' },
  });

  return (
    <Form {...form}>
      <form onSubmit={form.handleSubmit(onSubmit)} className="space-y-4">
        <FormField
          control={form.control}
          name="email"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Email</FormLabel>
              <FormControl>
                <Input type="email" placeholder="you@example.com" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <FormField
          control={form.control}
          name="password"
          render={({ field }) => (
            <FormItem>
              <FormLabel>Password</FormLabel>
              <FormControl>
                <Input type="password" placeholder="••••••••" {...field} />
              </FormControl>
              <FormMessage />
            </FormItem>
          )}
        />
        <Button type="submit" className="w-full bg-accent hover:bg-accent/90" disabled={loading}>
          {loading ? 'Processing...' : isSignUp ? 'Sign Up' : 'Login'}
        </Button>
      </form>
    </Form>
  );
}

function LoginView() {
  const { signInWithEmail, signUpWithEmail, loading } = useAuth();
  const [isSubmitting, setIsSubmitting] = React.useState(false);

  const handleLogin = async (values: LoginFormValues) => {
    setIsSubmitting(true);
    await signInWithEmail(values.email, values.password);
    setIsSubmitting(false);
  };

  const handleSignUp = async (values: LoginFormValues) => {
    setIsSubmitting(true);
    await signUpWithEmail(values.email, values.password);
    setIsSubmitting(false);
  };

  return (
    <main className="flex min-h-screen flex-col items-center justify-center p-4">
      <Header />
      <Card className="w-full max-w-sm shadow-xl">
        <CardHeader className="text-center">
          <CardTitle className="text-2xl">Welcome!</CardTitle>
          <CardDescription>Sign in or create an account to start earning.</CardDescription>
        </CardHeader>
        <CardContent>
          <Tabs defaultValue="login" className="w-full">
            <TabsList className="grid w-full grid-cols-2">
              <TabsTrigger value="login">Login</TabsTrigger>
              <TabsTrigger value="signup">Sign Up</TabsTrigger>
            </TabsList>
            <TabsContent value="login" className="pt-4">
              <AuthForm isSignUp={false} onSubmit={handleLogin} loading={isSubmitting || loading} />
            </TabsContent>
            <TabsContent value="signup" className="pt-4">
              <AuthForm isSignUp={true} onSubmit={handleSignUp} loading={isSubmitting || loading} />
            </TabsContent>
          </Tabs>
        </CardContent>
      </Card>
    </main>
  );
}

// --- Dashboard Component (merged) ---

const withdrawalSchema = z.object({
  amount: z.coerce.number().positive({ message: 'Amount must be positive.' }),
  gamepassLink: z.string().url({ message: 'Please enter a valid Gamepass URL.' }),
});

function Dashboard() {
  const { user, userData, signOut, requestWithdrawal, userWithdrawals } = useAuth();
  const [timer, setTimer] = useState(60);
  const [progress, setProgress] = useState(100);

  const isAdmin = user && (process.env.NEXT_PUBLIC_ADMIN_EMAIL === user.email || process.env.ADMIN_EMAIL === user.email);

  useEffect(() => {
    const interval = setInterval(() => {
      setTimer((prev) => (prev > 1 ? prev - 1 : 60));
    }, 1000);
    return () => clearInterval(interval);
  }, []);

  useEffect(() => {
    setProgress((timer / 60) * 100);
  }, [timer]);

  const form = useForm<z.infer<typeof withdrawalSchema>>({
    resolver: zodResolver(withdrawalSchema),
    defaultValues: { amount: 0, gamepassLink: '' },
  });

  async function onSubmit(values: z.infer<typeof withdrawalSchema>) {
    if (userData && values.amount > userData.balance) {
      form.setError('amount', { type: 'manual', message: 'Amount exceeds your balance.' });
      return;
    }
    const success = await requestWithdrawal(values.amount, values.gamepassLink);
    if (success) {
      form.reset();
    }
  }

  return (
    <div className="min-h-screen bg-background p-4 md:p-8">
      <header className="flex items-center justify-between max-w-6xl mx-auto">
        <div className="flex items-center gap-2">
            <RobuxIcon className="h-8 w-8 text-primary" />
            <span className="text-xl font-bold">Robux <span className="text-primary">Flow</span></span>
        </div>
        <div className="flex items-center gap-4">
            {isAdmin && (
              <Button asChild variant="outline">
                <Link href="/admin">
                  <Shield />
                  Admin Panel
                </Link>
              </Button>
            )}
            <div className="text-right">
                <p className="font-semibold text-lg">{userData?.name}</p>
                <p className="text-sm text-muted-foreground">{userData?.email}</p>
            </div>
          <Button onClick={signOut} variant="ghost" size="icon">
            <LogOut className="h-5 w-5" />
          </Button>
        </div>
      </header>

      <main className="mt-8 grid max-w-6xl mx-auto gap-8 md:grid-cols-2 lg:grid-cols-3">
        <Card className="lg:col-span-3 shadow-lg">
          <CardHeader>
            <CardTitle className="text-2xl">Your Balance</CardTitle>
            <CardDescription>This is your current Robux balance.</CardDescription>
          </CardHeader>
          <CardContent className="flex items-center gap-4">
            <RobuxIcon className="h-12 w-12 text-primary flex-shrink-0" />
            <p className="text-5xl font-bold tracking-tighter">
              {userData?.balance.toFixed(2) ?? '0.00'}
            </p>
          </CardContent>
        </Card>

        <Card className="shadow-lg">
          <CardHeader>
            <CardTitle>Robux Generator</CardTitle>
            <CardDescription>You earn +0.20 Robux every 60 seconds.</CardDescription>
          </CardHeader>
          <CardContent>
            <div className="space-y-2">
              <Progress value={progress} />
              <p className="text-center text-lg font-mono">{timer}s until next reward</p>
            </div>
          </CardContent>
        </Card>

        <Card className="shadow-lg">
          <Form {...form}>
            <form onSubmit={form.handleSubmit(onSubmit)}>
              <CardHeader>
                <CardTitle>Withdraw Robux</CardTitle>
                <CardDescription>Enter a Gamepass link and amount to withdraw.</CardDescription>
              </CardHeader>
              <CardContent className="space-y-4">
                <FormField
                  control={form.control}
                  name="amount"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Amount</FormLabel>
                      <FormControl>
                        <Input type="number" step="0.01" {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
                <FormField
                  control={form.control}
                  name="gamepassLink"
                  render={({ field }) => (
                    <FormItem>
                      <FormLabel>Gamepass Link</FormLabel>
                      <FormControl>
                        <Input placeholder="https://www.roblox.com/..." {...field} />
                      </FormControl>
                      <FormMessage />
                    </FormItem>
                  )}
                />
              </CardContent>
              <CardFooter>
                 <Button type="submit" className="w-full bg-accent hover:bg-accent/90">
                    <Send className="mr-2 h-4 w-4" /> Request Withdrawal
                 </Button>
              </CardFooter>
            </form>
          </Form>
        </Card>

        <Card className="lg:col-span-3 shadow-lg">
            <CardHeader>
                <CardTitle className="flex items-center gap-2"><History/> Withdrawal History</CardTitle>
                <CardDescription>Your recent withdrawal requests.</CardDescription>
            </CardHeader>
            <CardContent>
                <Table>
                    <TableHeader>
                        <TableRow>
                            <TableHead>Date</TableHead>
                            <TableHead>Amount</TableHead>
                            <TableHead>Status</TableHead>
                            <TableHead className="text-right">Gamepass Link</TableHead>
                        </TableRow>
                    </TableHeader>
                    <TableBody>
                        {userWithdrawals.length > 0 ? userWithdrawals.map(req => (
                            <TableRow key={req.id}>
                                <TableCell>{format(req.createdAt, "PPP p")}</TableCell>
                                <TableCell className="font-medium">{req.amount.toFixed(2)}</TableCell>
                                <TableCell>
                                    <Badge variant={
                                        req.status === 'pending' ? 'secondary' :
                                        req.status === 'completed' ? 'default' : 'destructive'
                                    } className={req.status === 'completed' ? 'bg-accent text-accent-foreground' : ''}>
                                        {req.status}
                                    </Badge>
                                </TableCell>
                                <TableCell className="text-right">
                                    <a href={req.gamepassLink} target="_blank" rel="noopener noreferrer" className="text-primary hover:underline truncate block max-w-[200px]">
                                        {req.gamepassLink}
                                    </a>
                                </TableCell>
                            </TableRow>
                        )) : (
                            <TableRow>
                                <TableCell colSpan={4} className="text-center">No withdrawal requests yet.</TableCell>
                            </TableRow>
                        )}
                    </TableBody>
                </Table>
            </CardContent>
        </Card>
      </main>
    </div>
  );
}


// --- Main Page Logic ---

export default function Home() {
  const { user, loading } = useAuth();

  if (loading) {
    return (
      <div className="flex min-h-screen flex-col items-center justify-center bg-background p-4">
        <RobuxIcon className="h-16 w-16 animate-spin text-primary" />
        <p className="mt-4 text-muted-foreground">Loading Your Flow...</p>
      </div>
    );
  }

  return user ? <Dashboard /> : <LoginView />;
}
